.bottom-section-container {
  display: flex;
  gap: 20px;
  align-items: flex-start;
  padding: 20px;
  box-sizing: border-box;
}

.bottom-section-main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.bottom-section-side-panel {
  width: 350px;
  flex-shrink: 0;
}
